@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  min-height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.app:before {
  content: '';
  background: url('./assests/background.webp') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}

.app .error {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  align-content: center;
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width: 700px;
  margin: auto;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2);
}

.bold {
  font-weight: 700;
}

@media (max-width: 768px) {
  h1 {
    font-size: 4rem;
  }

  p {
    font-size: 1.2rem;
  }

  .app input {
    font-size: 1rem;
  }

  .app .bottom {
    flex-direction: column;
    align-items: center;
  }

  .app .description {
    position: static;
    transform: none;
  }
}
